@media screen and ( min-width:  768px ) {
    .roomrates {
        font-size: 120% !important; } }


#pricing-chart-section {
    background-image: url("../../../images/twisting-img.gif");
    background-size: cover;
    background-attachment: fixed;
    color: white !important;
    * {
        color: white; } }


#rates {
    color: white !important;
    padding: 0px;
    width: 100vw;
    max-width: 100vw !important;
    overflow: hidden;
    * {
        color: white; }
    table {
        width: 100vw; }
    .roomrates {
        padding: 0px;
        margin: 0px;
        font-size: 80%; }

    .roomrates h2 {
        text-align: center;
        font-size: 120%; }


    .roomrates th {
        text-transform: uppercase;
        color: orangered;
        margin: 0.2px;
        padding: 0.1px;
        border: 0.1px double red; }


    .roomrates td {
        color: red;
        background: linear-gradient(50deg, orange 93%, white 97%, orange 97%);
        border: 2px solid black;
        padding: 2px 1px;
        text-align: center;
        border-radius: 8px; }


    tr td:nth-child(1) {
        border: none;
        padding: 2px;
        background-color: orange;
        color: white;
        border-radius: 10px;
        border: .5px solid black; }
    tr:nth-child(10) > td:nth-child(1) {
        background-color: red; }
    #roomRates > tr:nth-child(10) > td:nth-child(2) {
        background-color: orange;
        color: red; }
    tr td:nth-child(2) {
        border: none;
        padding: 4px;
        background-color: red;
        color: white;
        border-radius: 10px;
        border: 2px solid black; }
    #roomRates > tr:nth-child(10) > td:nth-child(3) {
        background-color: khaki; }
    tr:nth-child(4) {
        background-color: red; }


    tr:nth-child(5) {
        td:nth-child(1) {
            background-color: blue; } }
    #roomRates > tr:nth-child(10) > td:nth-child(4) {
        background-color: orange; }

    tr:nth-child(5) {
        td:nth-child(2) {
            background-color: green;
            color: orangered; } }

    td:nth-child(4) {
        background-color: khaki; }


    // tr:nth-child(5) td:nth-child(3)
    //     background-color: khaki

    // tr:nth-child(5) td:nth-child(4)
    //     background-color: lightgoldenrodyellow

    // tr:nth-child(5) td:nth-child(5)
    //     background-color: antiquewhite

    // td:nth-child(5)
    //     background-color: lightgoldenrodyellow

    // tr:nth-child(6) td:nth-child(5)
    //     background-color: aliceblue


    .picnicRates h2, .poolRates h2 {
        text-align: center;
        margin: 40px;
        font-size: 200%; }


    .picnicRates table {
        margin: 2%;
        width: 96%; }

    .poolRates {}


    .poolRates table {
        margin: 1%;
        width: 98%; }




    .campingRates {
        display: block;
        padding: 20px;
        background-color: #000000;
        color: white;
        font-size: 100%; }

    .campingRates h2 {
        display: block;
        margin: 20px;
        text-align: center; }


    .campingRates .packageInfo {
        display: flex;
        border: 2px solid white;
        padding: 20px;
        margin: 0px; }

    .campingRates .packageInfo p {
        width: 80%;
        font-size: 90%; }

    .campingRates .packageInfo span {
        width: 20%;
        background-color: #f783;
        display: flex;
        align-self: center;
        padding: 10px;
        border-radius: 10px; }

    .moreRates {
        display: block;
        padding: 20px;
        margin: 20px;
        background-color: wheat;
        border-radius: 20px;
        box-shadow: 2px 2px 20px gray; }


    .moreRates a {
        display: inline-block;
        text-decoration: none;
        text-shadow: 1px 1px violet; } }

